import { isPromise } from '../../../../core/object'
import { communication } from '../../../../core/backbone'
import microFrontendConfig from 'front-hub-application-host-configuration-prod'

let { events, emitters, listeners, globals } = communication()

globals.calls = []

const removeDuplicates = eventsArray => Array.from(new Set(eventsArray))

const checkEventsConfiguration = microFrontendConfig => {
  if (microFrontendConfig && microFrontendConfig.events) {
    if (microFrontendConfig.events.listeners) {
      listeners = removeDuplicates(microFrontendConfig.events.listeners)
    }
    if (microFrontendConfig.events.emitters) {
      emitters = removeDuplicates(microFrontendConfig.events.emitters)
    }
  }
}

function init() {
  checkEventsConfiguration(microFrontendConfig)
}

const callInitOnceByFunctionName = functionName => {
  const { calls } = globals
  if (!calls.includes(functionName)) {
    calls.push(functionName)
    init()
  }
}

const alreadyHasEvent = eventName => events.some(item => item[0] === eventName)

function checkIsPromise(value) {
  if (isPromise(value)) {
    value.then(data => {
      checkIsPromise(data)
      return data
    })
  }

  return null
}

export const emit = (event, value) => {
  callInitOnceByFunctionName('emit')

  checkIsPromise(events)

  events.forEach(item => {
    if (item[0] === event) {
      try {
        item[1].call(null, value)
      } catch (e) {
        console.log('error: ', e.message)
      }
    }
  })
  console.log('events from emit: ', events)

  return
}

export const useListen = (event, cb) => {
  callInitOnceByFunctionName('useListen')

  checkIsPromise(events)

  if (!alreadyHasEvent(event)) {
    events.push([event, cb])
  }
  console.log('events from useListen: ', events)
}

export default () => ({
  emit,
  useListen,
})
